// tags
div.tags {
    padding: 0 0 1em 0;
    font-size: @font-size-small;
}

a.label,
.label {
    display: inline-block;
    padding: .25em .5em;
    margin-top: .5em;
    margin-bottom: 0;
    margin-right: .2em;
    font-weight: normal;
    font-size: @font-size-small;
    line-height: 1;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    border: 0 !important;
    .box-shadows(0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12));
    text-shadow: none;
    -webkit-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;

    // Hover/focus state
    &:hover,
    &:focus {
        text-decoration: none;
        text-shadow: none;
        outline: 0;
        .box-shadows(0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19));
    }

    // Focus state for keyboard and accessibility
    &:focus {
        .tab-focus();
        outline: 0;
    }

    // Active state
    &.active,
    &:active {
        background-image: none;
        outline: 0;
        .box-shadows(0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12));
        text-shadow: none;
    }

}

a.label,
.label {
    color: @gray;
    background: @whitesmoke;
    &:hover,
    &:focus {
        color: @gray;
        background: @whitesmoke-light;
    }
    &.active,
    &:active {
        color: @gray;
        background: @whitesmoke-dark;
    }
}

// primary > brand color
.label-primary,
a.label-primary {
    color: @color-primary-text;
    background: @color-primary;
    &:hover,
    &:focus {
        color: @color-primary-text;
        background: lighten(@color-primary, 10%);
    }
    &.active,
    &:active {
        color: @color-primary-text;
        background: darken(@color-primary, 10%);
    }
}

// accent > link color
.label-accent,
a.label-accent  {
    color: @color-accent-text;
    background: @color-accent;
    &:hover,
    &:focus {
        color: @color-accent-text;
        background: lighten(@color-accent, 10%);
    }
    &.active,
    &:active {
        color: @color-accent-text;
        background: darken(@color-accent, 10%);
    }
}

// blank > white only shadows
.label-blank,
a.label-blank {
    color: @color-link;
    background: @smoke;
    .box-shadows(0 0 0 0 transparent, 0 0 0 0 transparent);
    &:hover,
    &:focus {
        color: @color-link-hover;
        background: @smoke;
    }
    &.active,
    &:active {
        color: @color-link-visited;
        background: @smoke;
    }
}

