// tags bootstrap list striped
ul.category,
ul.list-striped {
    list-style-type: none;
    width: 100%;
    margin: .5em 0;
    padding: 0;
    font-size: @font-size-small;
    border: 1px solid @gainsboro-light;

    li {
        list-style-type: none;
        position: relative;
        display: block;
        padding: 0;
        margin: 0;

        h3, h4 {
            font-size: 1.125em;
            margin: 0;
        }

        a {
            text-decoration: none;
            font-weight: normal;
            text-shadow: none;
            text-transform: uppercase;
            position: relative;
            float: left;
            display: block;
            width: 100%;
            padding: .5em 1em;
            margin: 0;
            color: @color-link;
            background: @smoke;
            border-bottom: 1px solid @gainsboro-light;

            -webkit-transition: all 0.3s ease-out;
            transition: all 0.3s ease-out;

            &:hover,
            &:focus {
                color: @color-link-hover;
                background: @gainsboro-lighter;
            }

            // &.active,
            &:active {
                color: @color-link-visited;
                background: @gainsboro-light;
            }

            &.active {
                color: @color-link-visited;
                background: @clouds;
            }
        }
    }
}

// show tags von php roberto überschreiben mit .content-tags
div.content-tags {
    margin: .5em 0;
    padding: 0;
    font-size: .875em;
    min-height: 1em;
    overflow: hidden;
    display: block;

    ul {
        list-style-type: none;
        width: 100%;
        margin: 0;
        padding: 0;

        li {
            margin: 0;
            padding: 0;

            a {
                display: inline-block;
                padding: .25em .5em;
                margin: 0 .25em .25em 0;
                float: left;
                color: @concrete-darker;
                background: @gainsboro-light;
                .border-radius(@baseBorderRadius);
                font-weight: normal;
                text-decoration: none;
                cursor: pointer;
                line-height: 1.4;

                &:hover {
                    color: @whitesmoke;
                    background: @concrete;
                }

                &:focus, &:active {
                    color: @whitesmoke;
                    background: @concrete-dark;
                }
            }
        }
    }
}